/* eslint-disable react/prop-types */
/* eslint-disable react/display-name */
/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Accordion } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";
import AccordionCard from "../accordionCard";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "Viewer", refs: [] };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const {
    helpPath3,
    contactMessageCardsCount,
    contactMessageCardsAttributes,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { setHelpPath } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  // eslint-disable-next-line no-unused-vars
  const { t } = useTranslation([props.namespace]);

  /**
   * Refs
   ============================================================================ */
  // const refViewer = [];
  // refViewer[0] = React.createRef();
  // refViewer[1] = React.createRef();

  /**
   * Effects
   ============================================================================ */
  // React.useEffect(() => {
  //   const text = props.text;
  //   if (refViewer[0].current !== null) {
  //     refViewer[0].current.innerHTML = text;
  //   }
  //   if (refViewer[1].current !== null) {
  //     refViewer[1].current.innerHTML = text;
  //   }
  //   // setContactMessage(text);

  //   // eslint-disable-next-line
  // }, []);

  /**
   * Functions
   ============================================================================ */
  const toggleCardClick = (attributes) => {
    if (typeof attributes === "undefined") return;

    setHelpPath(
      3,
      helpPath3 === attributes.eventkey ? "" : attributes.eventkey
    );
  };

  /**
   * Return
   ============================================================================ */
  if (contactMessageCardsAttributes === null) return <></>;
  return (
    <>
      <Accordion defaultActiveKey={helpPath3}>
        {Array(contactMessageCardsCount)
          .fill()
          .map((item, index) => {
            return (
              <AccordionCard
                key={index}
                title={
                  typeof contactMessageCardsAttributes[index] !== "undefined"
                    ? contactMessageCardsAttributes[index].title
                    : ""
                }
                eventkey={
                  typeof contactMessageCardsAttributes[index] !== "undefined"
                    ? contactMessageCardsAttributes[index].eventkey
                    : ""
                }
                active={helpPath3}
                onClick={() =>
                  toggleCardClick(contactMessageCardsAttributes[index])
                }
              >
                <>
                  <div ref={props.refs[index]} style={{}}></div>
                </>
              </AccordionCard>
            );
          })}
      </Accordion>
    </>
  );
};
